@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
.button{background:none;border-radius:5px;cursor:pointer;outline:none;transition:0.5s;padding:10px;text-decoration:none;text-transform:uppercase}.button:hover{background-color:#4f98ca;color:white;transform:scale(1.1)}.white{border:2px solid #fff;color:#fff}.white:hover{border:2px solid #4f98ca;transition:all 0.3s ease-in-out}.secondary{border:2px solid #4f98ca;color:#4f98ca}.secondary:hover{border:2px solid #4f98ca;transition:all 0.3s ease-in-out}body{margin:0;font-family:"Muli",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#effffb}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.home__info{display:flex;flex-direction:row;justify-content:space-between;align-items:flex-end;padding:15px}.home__info span{color:#272727;font-size:medium;font-weight:bold}.home__info button{background:none;border:solid 2px #4f98ca;border-radius:5px;color:#4f98ca;cursor:pointer;height:30px;outline:none;transition:.5s;width:100px}.home__info button:hover{background-color:#4f98ca;border:none;color:white;transform:scale(1.1)}.home__grid{display:grid;grid-template-columns:repeat(1, auto)}@media only screen and (min-width: 768px){.home__grid{display:grid;grid-template-columns:repeat(2, auto)}}@media only screen and (min-width: 992px){.home__grid{display:grid;grid-template-columns:repeat(5, auto)}}

.card{border-radius:10px;border:solid 1px #ddd;display:flex;flex-direction:row;height:50px;margin:5px;align-items:center;justify-self:stretch}.card__img{object-fit:cover;-o-object-fit:cover;height:50px;width:50px}.card__info{margin-left:5px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.card__info span{color:#272727;font-size:small;font-weight:bold}

.progress-line,.progress-line:before{height:3px;width:100%;margin:0}.progress-line{background-color:#b3d4fc;display:flex}.progress-line:before{background-color:#5292f7;content:'';-webkit-animation:running-progress 2s cubic-bezier(0.4, 0, 0.2, 1) infinite;animation:running-progress 2s cubic-bezier(0.4, 0, 0.2, 1) infinite}@-webkit-keyframes running-progress{0%{margin-left:0px;margin-right:100%}50%{margin-left:25%;margin-right:0%}100%{margin-left:100%;margin-right:0}}@keyframes running-progress{0%{margin-left:0px;margin-right:100%}50%{margin-left:25%;margin-right:0%}100%{margin-left:100%;margin-right:0}}

.header{align-items:center;background:#50d890;color:white;display:flex;height:70px;justify-content:space-between;top:0px;width:100%}.header__img{margin-left:5px;object-fit:contain;width:200px}.header__menu{-webkit-tap-highlight-color:rgba(0,0,0,0);cursor:pointer;display:flex;flex-direction:row;margin-right:5px;outline:none}.header__menu img{background-position:center;border-radius:30px;margin:5px;object-fit:contain;padding:8px;transition:background 0.8s;width:35px}.header__menu img:hover{background:#84e3b1 radial-gradient(circle, transparent 1%, #84e3b1 1%) center/15000%}.header__menu img:active{background-color:#effffb;background-size:100%;transition:background 0s}

.NotFound{display:flex;width:100%;height:100%;justify-content:center}

.spreadSheet{height:calc(100vh - 70px);scroll-behavior:unset}

.cell{background-color:white;border:1px #eeeeee solid;box-sizing:border-box;color:#272727;font-size:14px;height:25px;line-height:15px;outline:none;overflow:hidden;padding:4px;position:relative;text-align:left;width:100px}.cell--selected{border-color:#4f98ca;border:2px #5292f7 solid;box-shadow:0 2px 6px 2px rgba(60,64,67,0.15);outline-style:none}.cell.header{background-color:#effffb;color:gray;display:inline-block;font-size:x-small;font-weight:bold;text-align:center}

.login{align-items:center;background:linear-gradient(#50d890, #4f98ca);display:flex;flex-direction:column;justify-content:center;width:100vw;height:100vh;min-width:100vw;min-height:100vh}.login__container{display:flex;flex-direction:column;color:white}.login__container button{margin-top:50px}@media only screen and (min-width: 768px){.login__container{background-color:rgba(255,255,255,0.1);display:flex;flex-direction:column;min-height:450px;min-width:450px;align-items:center;color:white;justify-content:space-evenly}}

